@import '../mixin/link-btn';
@import '../mixin/page-title';
@import '../mixin/scroll-area';
@import '../mixin/plus-icon';

$black-font:#262626;
$gley-font: #88909b;



.back-imgarea--service{
  background-image:url(/img/link-service.jpg);
  margin-bottom: 140px;
  height: 70vh;
}


.service-web{
  &__container{
    padding-bottom: 140px;
    display: flex;
    justify-content: space-between;
    position: relative;
  }
  &__titlearea{
    width: 33%;
    padding-left: 6.6%;
  }
  &__title{
    font-size: 1.6rem;
    letter-spacing: 0.37em;
    &--en{
      display: block;
      font-family: 'Barlow Condensed', sans-serif;
      font-weight: 600;
      letter-spacing: 0.55em;
      font-size: 2.8rem;
      line-height: 1.5;
    }
    &--ja{
      padding: 10px 0 0;
      font-weight: 500;
    }
  }
  &__txtarea{
    width: 67%;
    padding-right: 6.6%;
  }
  &__exp{
    font-size: 1.6rem;
  }
  &__list{
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item{
    width: 46%;
    margin-top: 35px;
  }
  &__num{
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    font-size: 1.2rem;
  }
  &__enname{
    line-height: 1.5;
    margin-top: 5px;
    border-top: 1px solid #a4a5a7;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    font-size: 2.0rem;
    letter-spacing: 0.2em;
    padding: 10px 0 5px;
  }
  &__janame{
    line-height: 1.5;
    margin-top: 5px;
    font-size: 1.2rem;
    font-weight: 500;
  }
  &__btn{
    position: absolute;
    right: 6.588%;
    bottom: -30px;
    z-index: 1000;
    .link-btn{
      @include _link-btn;
    }
  }
}


.service-dt{
  padding-top: 100px;
  padding: 120px;
  background: #f5f6f7;
  &__container{
    padding: 0 6.6%;
  }
  &__title{
    margin-bottom: 50px;
    letter-spacing: 0.35em;
    font-size: 2.2rem;
    font-weight: 500;
    text-align: center;
  }
  &__list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item{
    width: 24%;
    background: #FFFFFF;
    font-size: 1.3rem;
    padding: 55px 25px 30px;
    box-sizing: border-box;
    margin-bottom: 20px;
  }
  &__img{
    width: 65px;
    margin: 0 auto;
    margin-bottom: 20px;
  }
  &__name{
    text-align: center;
    font-size: 1.6rem;
    font-weight: 500;
    margin-bottom: 8px;
  }
  &__exp{
    font-size: 1.3rem;
  }
  &__costarea{
    margin-top: 80px;
    padding: 40px 0;
    border: 1px solid #9ea0a2;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__costleft{
    width: 23%;
    text-align: center;
    font-weight: 500;
    font-size: 1.8rem;
  }
  &__costright{
    width: 77%;
    padding: 0 7%;
    border-left: 1px solid #9ea0a2;
    text-align: center;
  }
  &__costlist{
    border-top: 1px solid #9ea0a2;
    padding-top: 25px;
    margin-top: 25px;
    display: flex;
    justify-content: center;
  }
  &__costitem{
    font-weight: 500;
    line-height: 1.5;
    letter-spacing: 0.20em;
    margin: 0 3%;
  }
  &__cost{
    font-size: 2.0rem;
    font-weight: 600;
  }
}


.service-br{
  padding: 120px 0;
  text-align: center;
  &__container{
    padding: 0 6.6%;
  }
  &__title{
    font-size: 1.6rem;
    letter-spacing: 0.37em;
    &--en{
      display: block;
      font-family: 'Barlow Condensed', sans-serif;
      font-weight: 600;
      letter-spacing: 0.55em;
      font-size: 2.8rem;
      line-height: 1.5;
    }
    &--ja{
      padding: 10px 0 0;
      font-weight: 500;
    }
  }
  &__exp{
    font-size: 1.6rem;
    letter-spacing: 0.30em;
    padding: 30px 0 50px;
  }
  &__imgarea{
    display: flex;
    justify-content: center;
    border: 1px solid #9ea0a2;
    padding: 60px 7% 50px;
    margin-bottom: 20px;
    align-items: center;
  }
  &__list{
    display: flex;
    justify-content: center;
  }
  &__item{
    padding: 0 30px;
  }
  &__img{
    display: block;
    width: 65px;
    margin: 0 auto 20px;
  }
  &__plusicon{
    @include _plus-icon;
  }
  &__txt{
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 1.75;
  }
  &__note{
    font-size: 1.2rem;
  }
}


.service-fl{
  padding: 120px 6.6%;
  background-color: #f5f6f7;
  &__title{
    width: 100%;
    font-size: 1.6rem;
    letter-spacing: 0.37em;
    text-align: center;
    position: relative;
    &--en{
      display: block;
      font-family: 'Barlow Condensed', sans-serif;
      font-weight: 600;
      letter-spacing: 0.55em;
      font-size: 2.8rem;
      line-height: 1.5;
    }
    &--ja{
      display: block;
      padding: 10px 0 0;
      font-weight: 500;
    }
  }
  &__list{
    display: flex;
    justify-content: space-between;
    padding: 40px 0 50px;
  }
  &__item{
    width: 14.28%;
    height: 450px;
    background: #FFFFFF;
    margin-right: 2px;
    position: relative;
    transition: all .3s;
    .plus{
      margin: 0;
      position: absolute;
      left: 50%;
      bottom: 40px;
      transform: translateX(-50%);
      background: #f5f6f7;
      border-radius: 50%;
      width: 30px;
      height: 30px;
      &::before, &::after{
        content: "";
        width: 12px;
        height: 2px;
        background: #1c1f22;
        position: absolute;
        left: 9px;
        top: 14px;
        transform-origin: center center;
      }
      &::after{
        transform:rotate(90deg)
      }
    }
    &.open{
      width: 50% !important;
      color: #FFFFFF;
      background: #494c4e;
    }
    &.open .plus::after{
      display: none;
    }
  }
  &__block{
    padding: 35px 10% 25px;
    position: relative;
    transition: all .3s;
  }
  &__no{
    font-size: 1.4rem;
    font-weight: 500;
    padding-bottom: 10px;
  }
  &__name{
    font-size: 1.2rem;
    font-weight: 400;
    span{
      display: block;
      font-family: 'Barlow Condensed', sans-serif;
      font-weight: 600;
      font-size: 1.8rem;
      margin-bottom: 5px;
    }
  }
  &__exp{
    display: none;
    padding: 20px 10%;
    position: relative;
    &::before{
      position: absolute;
      content: "";
      width: 30px;
      height: 1px;
      background: #ffffff;
      left: 10%;
      top: 0;
    }
  }
  &__arrow{
    width: 92%;
    display: flex;
    justify-content: space-between;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
    letter-spacing: 0.25em;
    margin: 0 auto;
    border-top: 1px solid #1c1f22;
    &--st{
      width: 50%;
      position: relative;
      padding-top: 10px;
      &::after{
        position: absolute;
        content:"";
        width: 9px;
        height: 9px;
        background: #1c1f22;
        left: 0;
        top: -5px;
        border-radius: 50%;
      }
    }
    &--fn{
      width: 50%;
      position: relative;
      padding-top: 10px;
      text-align: right;
      &::after{
        position: absolute;
        content:"";
        width: 0;
        height: 0;
        right: 0;
        top: -10px;
        border-style: solid;
        border-width: 10px 0 0 10px;
        border-color: transparent transparent transparent #1c1f22;
      }
    }
  }
}

.back-imgarea--service-bt{
  height: 480px;
  background-image: url(/img/service_img.jpg);
}


.contract{
  padding: 120px 0;
  background: #777d85;
  color: #FFFFFF;
  text-align: center;
  &__container{
    padding: 0 6.6%;
  }
  &__title{
    width: 100%;
    &--en{
      font-family: 'Barlow Condensed', sans-serif;
      display: block;
      font-size: 2.8rem;
      letter-spacing: 0.55em;
      line-height: 1.5;
    }
    &--ja{
      display: block;
      font-size: 1.6rem;
      letter-spacing: 0.37em;
      padding: 10px 0 0;
      font-weight: 500;
    }
  }
  &__exp{
    font-size: 1.6rem;
    line-height: 2;
    letter-spacing: 0.30em;
    padding: 30px 0 50px;
  }
  &__area{
    color:$black-font;
    background:white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 60px 7% 55px;
  }
  &__workarea{
    width: 42%;
  }
  &__worktitle{
    font-size: 1.3rem;
    font-weight: 500;
    line-height: 1.75;
    margin-bottom: 30px;
    span{
      display: block;
      font-family: 'Barlow Condensed', sans-serif;
      font-weight: 600;
      font-size: 2.4rem;
      letter-spacing: 0.20em;
    }
  }
  &__list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 10px;
  }
  &__item{
    width: 48%;
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 600;
    border-bottom: 1px solid #d2d2d3;
    padding: 10px 0;
    &:nth-child(1), &:nth-child(2){
      border-top: 1px solid #d2d2d3;
    }
  }
  &__more{
    font-family: 'Barlow Condensed', sans-serif;
    font-weight: 500;
    text-align: right;
  }
  &__plus-icon{
    @include _plus-icon;
  }
}