}

.service-web__title--en {
  display: block;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: 0.55em;
  font-size: 2.8rem;
  line-height: 1.5;
}

.service-web__title--ja {
  padding: 10px 0 0;
  font-weight: 500;
}

.service-web__txtarea {
  width: 67%;
  padding-right: 6.6%;
}

.service-web__exp {
  font-size: 1.6rem;
}

.service-web__list {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.service-web__item {
  width: 46%;
  margin-top: 35px;
}

.service-web__num {
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
}

.service-web__enname {
  line-height: 1.5;
  margin-top: 5px;
  border-top: 1px solid #a4a5a7;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  font-size: 2.0rem;
  letter-spacing: 0.2em;
  padding: 10px 0 5px;
}

.service-web__janame {
  line-height: 1.5;
  margin-top: 5px;
  font-size: 1.2rem;
  font-weight: 500;
}

.service-web__btn {
  position: absolute;
  right: 6.588%;
  bottom: -30px;
  z-index: 1000;
}

.service-web__btn .link-btn {
  display: inline-block;
  width: 360px;
  line-height: 65px;
  text-align: right;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  font-size: 1.4rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background: #1c1f22;
  color: #FFFFFF;
  letter-spacing: 0.2em;
  box-sizing: border-box;
  transition: .2s;
}

.service-web__btn .link-btn-line {
  display: block;
  width: 30px;
  height: 1px;
  background: #FFFFFF;
  position: absolute;
  right: 45px;
  top: 50%;
  transition: .2s;
}

.service-web__btn .link-btn::before {
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
}

.service-web__btn .link-btn::after {
  position: absolute;
  display: block;
  content: "";
  width: 0;
  height: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  right: 0;
  bottom: 0;
}

.service-web__btn .link-btn-bd {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding-right: 95px;
  transition: .2s;
}

.service-web__btn .link-btn-bd:hover {
  padding-right: 115px;
}

.service-web__btn .link-btn-bd::before {
  position: absolute;
  display: block;
  content: "";
  height: 0;
  width: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  left: 0;
  bottom: 0;
}

.service-web__btn .link-btn-bd::after {
  position: absolute;
  display: block;
  content: "";
  height: 0;
  width: 1px;
  background-color: #1c1f22;
  transition: all .2s ease-out;
  top: 0;
  right: 0;
}

.service-web__btn .link-btn:hover {
  background: rgba(28, 31, 34, 0);
  color: #262626;
  opacity: 1;
}

.service-web__btn .link-btn:hover .link-btn-line {
  width: 50px;
  background: #1c1f22;
}

.service-web__btn .link-btn:hover::before {
  width: 100%;
  transition-delay: .1s;
}

.service-web__btn .link-btn:hover::after {
  width: 100%;
  transition-delay: .3s;
}

.service-web__btn .link-btn:hover .link-btn-bd::before {
  height: 100%;
  transition-delay: .4s;
}

.service-web__btn .link-btn:hover .link-btn-bd::after {
  height: 100%;
  transition-delay: .2s;
}

