  position: relative;
}

.service-fl__title--en {
  display: block;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 600;
  letter-spacing: 0.55em;
  font-size: 2.8rem;
  line-height: 1.5;
}

.service-fl__title--ja {
