  background: #FFFFFF;
  margin-right: 2px;
  position: relative;
  transition: all .3s;
}

.service-fl__item .plus {
  margin: 0;
  position: absolute;
  left: 50%;
  bottom: 40px;
  transform: translateX(-50%);
  background: #f5f6f7;
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.service-fl__item .plus::before, .service-fl__item .plus::after {
  content: "";
  width: 12px;
  height: 2px;
  background: #1c1f22;
  position: absolute;
  left: 9px;
  top: 14px;
  transform-origin: center center;
}

.service-fl__item .plus::after {
  transform: rotate(90deg);
}

.service-fl__item.open {
  width: 50% !important;
  color: #FFFFFF;
  background: #494c4e;
}

.service-fl__item.open .plus::after {
  display: none;
}

